<template>
  <div id="app">
    <header>
      <div id="logo" class="align-center">
        <img src="./assets/logo.png">
        <h1>{{ msg }}</h1>
      </div>
    </header>
    <main>
      <div id="description" class="align-center">
        <h1>Сервис для создания прописей</h1>
        <p>Сервис для создания прописей имеет множество настроек, попробуйте различные комбинации и подготовьте пропись в формате
        PDF. Останется только её распечатать. Есть возможность вставить нотный стан. Сервис работает как с кириллическими, так и с латинскими символами. Есть план по улучшению и добавлению новых возможностей.
        </p>
      </div>
      <form class="decor align-center" action="gotopdf2.php" method="post">
        <aside id="flex-block-1" class="sidebar">
          <div class="form-left-decoration">
            <div id="writing-type">
              <h3>Выберите тип написания букв</h3>
              <p>
                серый <input type="radio" name="type_letter_propisi" value=0 checked>
                пунктирный <input type="radio" name="type_letter_propisi" value=1>
              </p>
            </div>
            <div id="writing-lang">
              <h3>Выберите тип символов</h3>
              <p>
                кириллические <input type="radio" name="lang_letter_propisi" value=0>
                латинские <input type="radio" name="lang_letter_propisi" value=1  checked>
              </p>
            </div>
            <div id="writing-lines">
              <h3>Оформление страницы</h3>
              <p>
                линии <input type="checkbox" name="lines" checked="checked" value="Yes">
                сверху <input type="checkbox" name="line_up" checked="checked" value="Yes">
                снизу <input type="checkbox" name="line_down" checked="checked" value="Yes"></p>
              <p>
                наклонная <input type="checkbox" name="inclined_line" checked="checked" value="Yes">
                поля <input type="checkbox" name="border_line" checked="checked" value="Yes">

              </p>
            </div>
            <div id="writing-margin">
              <h3>Отступы линий</h3>
              <p>
                слева <select name="margin_left">
                <option value="1">1px</option>
                <option value="2">2px</option>
                <option value="3">3px</option>
                <option value="4">4px</option>
                <option value="5" selected="selected">5px</option>
              </select>
                справа <select name="margin_right">
                <option value="1">1px</option>
                <option value="5" selected="selected">5px</option>
                <option value="10">10px</option>
                <option value="15">15px</option>
                <option value="20">20px</option>
                <option value="25">25px</option>
              </select>
                сверху <select name="margin_top">
                <option value="1">1px</option>
                <option value="2">2px</option>
                <option value="3">3px</option>
                <option value="4">4px</option>
                <option value="5" selected="selected">5px</option>
              </select>
              </p>
            </div>
            <div id="grid-height">
              <h3>Сетка позиционирования</h3>
              <p>
                Высота (сетки)
                <select name="grid">
                  <option value="10">10</option>
                  <option value="11">11</option>
                  <option value="12">12</option>
                  <option value="13">13</option>
                  <option value="14">14</option>
                  <option value="15">15</option>
                  <option value="16">16</option>
                  <option value="17">17</option>
                  <option value="18" selected="selected">Стандартная (18)</option>
                  <option value="19">19</option>
                  <option value="20">20</option>
                  <option value="21">21</option>
                  <option value="22">22</option>
                  <option value="23">23</option>
                  <option value="24">24</option>
                  <option value="25">25</option>
                </select>
              </p>

            </div>
            <div id="writing-stave">
              <h3>Нотный стан</h3>
              <p>
                нотный стан <input type="checkbox" name="stave" checked="checked" value="Yes">
              </p>
              <p>
                Смещение текст-ноты
                <select name="offset_t_n">
                  <option value="1">-3</option>
                  <option value="2">-2</option>
                  <option value="3">-1</option>
                  <option value="4" selected="selected">Центр</option>
                  <option value="5">+1</option>
                  <option value="6">+2</option>
                  <option value="7">+3</option>
                </select>
              </p>
            </div>
          </div>
        </aside>
        <div id="flex-block-2" class="content">
          <div class="form-right-decoration"></div>
          <div class="circle"></div>
          <div class="form-inner">
            <h3>Текст, который мы превратим в пропись:</h3>
            <p><textarea id="writing-text" name="data1" rows="10"></textarea></p>
            <input id="submit" type="submit" value="Создать пропись">
          </div>

        </div>
      </form>
    </main>
    <footer>
      <div id="footer-line" class="align-center">
        <div id="footer-info">
          По вопросам работы сайта можно обращаться в службу поддержки <a href="mailto:kolelan@yandex.ru">kolelan@yandex.ru</a>
        </div>
      </div>
    </footer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      msg: 'Внимание, концентрация, ум!',
      posts: [
        {
          title: 'Title 1',
          body: 'Body 1'
        },
        {
          title: 'Title 2',
          body: 'Body 2'
        },
        {
          title: 'Title 3',
          body: 'Body 3'
        },
      ]
    }
  }
}
</script>

<style lang="scss">
body {
  margin: 0;
  padding: 0;
  min-width: 360px;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin: 0;
  padding: 0;
}

#description {
  text-align: left;
}

.align-center {
  max-width: 1028px;
  margin: 0 auto;
}

header {
  color: aliceblue;
  background-color: #2c3e50;

  #logo {

    padding: 18px;
    background-color: #2c3e50;
    display: flex;
    align-items: center;

    img {
      width: 120px;
      height: 120px;
    }

    h1 {
      margin: 10px;
      padding-left: 30px;
    }
  }
}

main {
  padding: 10px;

  .content {
    padding-left: 10px;

    #writing-text {
      width: 100%;
      min-height: 420px;
      padding: 5px;
    }

  }

  form {
    display: flex;

    #flex-block-1 {
      flex: 1 1 auto;
    }

    #flex-block-2 {
      flex: 1 1 auto;
    }

    .form-left-decoration {
      text-align: left;
    }

    #submit {
      margin-top:20px;
      box-shadow: inset 0px 0px 15px 3px #23395e;
      //background:linear-gradient(to bottom, #2c3e50 5%, #395182 100%);
      background-color: #2c3e50;
      border-radius: 7px;
      border: 1px solid #1f2f47;
      display: inline-block;
      cursor: pointer;
      color: #ffffff;
      font-size: 20px;
      padding: 10px 90px 10px 80px;
      text-decoration: none;
      text-shadow: 0px 1px 0px #17213f;
    }

    #submit:hover {
      background: linear-gradient(to bottom, #2c3e50 5%, #4a7cd0 100%);
      background-color: #385370;
    }

    #submit:active {
      position: relative;
      top: 1px;
    }


  }
}

h1, h2 {
  font-weight: normal;
}

h3 {
  margin-top: 27px;
  margin-bottom: 8px;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #385370;
}
p {
  margin: 8px 0;
}
footer {
  background-color: #2c3e50;
}
#footer-line {
  margin-top: 20px;
  padding-top: 23px;
  padding-bottom: 23px;
  font-size: 9pt;
  color: white;
  a {
    color: chocolate;
  }

}
</style>



